export default "html {\n  box-sizing: border-box;\n  width: 100%;\n  height: 100%;\n}\n\n*:before,\n*:after {\n  box-sizing: inherit;\n}\n\n/* Position */\n\n.pos-s      { position: static; }\n.pos-r      { position: relative; }\n.pos-a      { position: absolute; }\n.pos-f      { position: fixed; }\n.pos-sticky { position: sticky; }\n\n/* Object-Fit */\n\n.of-fill       { object-fit: fill; }\n.of-contain    { object-fit: contain; }\n.of-cover      { object-fit: cover; }\n.of-none       { object-fit: none; }\n.of-scale-down { object-fit: scale-down; }\n\n/* Object Position */\n\n.op-top           { object-position: left; }\n.op-right         { object-position: right; }\n.op-bottom        { object-position: bottom; }\n.op-left          { object-position: left; }\n.op-center        { object-position: center; }\n.op-left-top      { object-position: left top; }\n.op-center-top    { object-position: center top; }\n.op-right-top     { object-position: center top; }\n.op-left-bottom   { object-position: left bottom; }\n.op-center-bottom { object-position: center bottom; }\n.op-right-bottom  { object-position: center bottom; }";